.albums {
    width: 0;
    height: calc(100vh - 58px);
    overflow: auto;
    transition: 1s;
    border-left: 1px solid rgba(0, 0, 0, 0.87);
    transition: 0.5s;

    &.small {
      width: 64px;
    }
  
    &:hover,
    &.expand {
      width: 400px;
      transition: 0.5s;

      header {
        height: 350px;
        text-align: center;
        transition: 0.5s;
        opacity: 1;
      }
    }

    header {
      height: 0;
      transition: 0.5s;
      opacity: 0;
      position: relative;
      
      h2 {
        margin: 0;
        border: 0;
        background-color: rgba(1,1,1,0.5);
        color: white;
        position: absolute;
        bottom: 60px;
      }

      img {
        width: 300px; 
        height: 300px;
        border-radius: 50%;
        text-align: center;
        transition: 0.5s;
      }

      
    }
    
    div {
      display: flex;
      flex-direction: row;
      align-items: center;
      padding: 8px;
      height: 50px;
      width: 400px;

      img, 
      .icon {
        width: 50px;
        height: 50px;
        padding-right: 5px;
      }
    
      h3 {
        padding: 0;
        margin: 0;
      }
    }
  }