.songs {
    width: 0;
    height: calc(100vh - 58px);
    overflow: auto;
    border-left: 1px solid rgba(0, 0, 0, 0.87);
    border-right: 1px solid rgba(0, 0, 0, 0.87);
    transition: 0.5s;

    &.expand {
      width: 400px;
    }

    header {
      display: block;
      text-align: center;

      h2 {
        background-color: rgba(1,1,1,0.5);
      }
    }
    
    div {
      display: flex;
      flex-direction: row;
      align-items: center;
      padding: 8px;
      height: 50px;
      width: 400px;
    }
  
    h2 {
      padding: 0;
      margin: 0;
    }
  }