.artists {
  width: 0;
  height: calc(100vh - 58px);
  overflow: auto;
  transition: 0.5s;

  &.small {
    width: 64px;
  }

  &:hover,
  &.expand {
    width: 316px;
    transition: 0.5s;

  }
  
  .artist {
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 8px;
    height: 50px;
    width: 300px;
  }

  img, 
  .icon {
    border-radius: 50%; 
    width: 50px;
    height: 50px;
    padding-right: 5px;
  }

  h2 {
    padding: 0;
    margin: 0;
    // display: inline-block;
  }
}