.lyrics {
    max-width: 100%;
    height: 100%;
    overflow-y: auto;
    white-space: pre-line;
    text-align: center;
    display: inline-block;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    flex-grow: 1;

    article {
        backdrop-filter: brightness(40%);
        color: white;
        font-size: 1.2em;

        .noChoice {
            width: 100%;
            height: 100vh;
        }

        h2 {
            margin-top: 0;
            padding-top: 25px;
        }
        p {
            margin: 0;
            padding-bottom: 25px;
        }
    }

    
}